import React from "react";
import { Button, Form, Input, Select, Row, Col, Space, DatePicker } from "antd";
import moment from "moment";
const { Option } = Select;
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};
const { RangePicker } = DatePicker;

function SearchFrom(props) {
  const [form] = Form.useForm();
  const { handleSbumit, loading } = props;
  // 提交
  const onFinish = (values) => {
    handleSbumit(values);
  };
  // 重置
  const onReset = () => {
    form.resetFields();
  };

  // 表单项填写默认值
  //   const onFill = () => {
  //     form.setFieldsValue({
  //       note: "Hello world!",
  //       gender: "male",
  //     });
  //   };

  return (
    <Form {...layout} form={form} name="control-hooks" onFinish={onFinish}>
      <Row gutter={16}>
        <Col span={6}>
          <Form.Item name="publishdate" label="发表时间">
            <RangePicker
              ranges={{
                今天: [moment(), moment()],
                当月: [moment().startOf("month"), moment().endOf("month")],
              }}
              showTime
              format="YYYY/MM/DD HH:mm:ss"
            />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item name="tapic" label="类型">
            <Select placeholder="选择关键项" allowClear>
              <Option value="male">前端</Option>
              <Option value="female">摄影</Option>
              <Option value="other">编程</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item name="publishman" label="发表人">
            <Input />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item name="author" label="作者">
            <Input />
          </Form.Item>
        </Col>
      </Row>

      <Row gutter={16}>
        <Col span={6}>
          <Form.Item name="approalstatus" label="审核状态">
            <Select placeholder="选择关键项" allowClear>
              <Option value="male">通过</Option>
              <Option value="female">不通过</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item name="title" label="标题">
            <Input placeholder="输入文章标题" />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item name="zan" label="点赞">
            <Input />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item name="view" label="预览">
            <Input />
          </Form.Item>
        </Col>
      </Row>
      <Row justify="end">
        <Col span={8} align="end">
          <Form.Item {...tailLayout}>
            <Space>
              <Button loading={loading} type="primary" htmlType="submit">
                搜索
              </Button>
              <Button htmlType="button" onClick={onReset}>
                重置
              </Button>
            </Space>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default SearchFrom;
