import { useState, useEffect } from "react";

import SearchFrom from "../../../components/SearchForm/index";
import CommonTable from "../../../components/CommonTable/index";
function Log() {
  const [loading, setLoading] = useState(false);
  const handleSbumit = (v) => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      console.log(v);
    }, 3000);
  };
  useEffect(() => {
    handleSbumit();
  }, [loading]);
  return (
    <div>
      <SearchFrom handleSbumit={handleSbumit} loading={loading} />
      <CommonTable
        loading={loading}
        paginations={{
          size: "small",
          hideOnSinglePage: true,
          showSizeChanger: true,
          showQuickJumper: true,
          pageSize: 10,
          pageSizeOptions: [10, 20, 30, 40],
        }}
      />
    </div>
  );
}

export default Log;
